import React, { Component } from "react"
import Collapsible from "react-collapsible"
//import MidTown from "../components/data.json"

class postBuilder extends Component {
  render() {
    const listItems = this.props.locations.map((
      number //TODO add images...
    ) => (
      <Collapsible trigger={number.name} classParentString="midtownLunch">
        <div class="row" style={{ display: "inline" }}>
          <a id="menu" href={number.website} style={{ float: "left" }}>
            Menu
          </a>
          <p style={{ float: "left" }}>{number.formatted_address}</p>{" "}
        </div>
        <p>Review: {number.review}</p>
      </Collapsible>
    ))
    return <ul>{listItems}</ul>
  }
}
export default postBuilder
